@import "../utils/mixins";
@import "../utils/vars";
@import "../utils/extends";

.testimonials {
  margin-bottom: 120px;

  @include onTablet {
    margin-bottom: 148px;
  }

  &__suptitle {
    margin: 0 0 16px;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #c0cdd7;
  }

  &__title {
    margin: 0 0 48px;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    text-align: center;
    color: $colorTitle;

    @include onTablet {
      margin: 0 0 56px;
      text-align: start;
      width: 200px;
    }

    @include onDesctop {
      margin: 0 0 85px;
    }
  }

  &__cards {
    @include onDesctop {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 30px;
    }
  }

  &__card {
    margin-bottom: 24px;
    padding: 75px 38px 32px;
    border-radius: 16px;
    background-color: $colorWhite;
    border: 1px solid transparent;
    box-shadow: 0 9px 18px rgba(37, 41, 49, 0.03);
    transition: transform 0.3s;

    &:last-child {
      margin-bottom: 0;
    }

    &:hover {
      transform: scale(1.05);
    }

    @include onTablet {
      margin-bottom: 30px;
      padding: 87px 98px 32px;
    }

    @include onDesctop {
      margin-bottom: 0;
      padding: 75px 48px 32px;
      width: 20%;
    }

    @include onDesctopLarge {
      padding: 87px 56px 32px;
      width: 21%;
    }
  }

  &__photo {
    position: relative;
    margin: 0 auto 70px;
    width: 110px;
    height: 110px;

    @include onTablet {
      width: 140px;
      height: 140px;
      margin-bottom: 82px;
    }

    @include onDesctop {
      margin: 0 auto 70px;
      width: 110px;
      height: 110px;
    }

    @include onDesctopLarge {
      width: 140px;
      height: 140px;
      margin-bottom: 82px;
    }

    &::before {
      @extend %photoImage;
      top: 0;
      right: 40%;
    }

    &::after {
      @extend %photoImage;
      left: 40%;
      bottom: 0;
    }

    @each $name, $color in $colors {
      &--#{$name} {
        &::before,
        &::after {
          background-color: $color;
        }
      }
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    position: relative;
    z-index: 3;
    object-fit: cover;
    object-position: center;
  }

  &__icon {
    display: block;
    margin: 0 auto 10px;
    width: 20px;
    height: 18px;
  }

  &__phrase {
    margin: 0 auto 16px;
    max-width: 362px;
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;
  }

  &__name {
    margin: 0 0 4px;
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    color: $colorTitle;
  }

  &__role {
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #c0cdd7;
  }
}
