@import "../utils/mixins";

.menu {
  background-color: $backgroundColor;

  &__content {
    height: 100vh;
    box-sizing: border-box;
    padding: 21px 0 0;

  }

  &__header {
    display: flex;
    align-items: center;
    column-gap: 36px;
  }

  &__icon {
    width: 18px;
    height: 18px;

    @include onDesctop {
      display: none;
    }
  }

  &__menu-icon {
    display: block;
    background-image: url(../images/features/menu-close.svg);
    @extend %objectCenter;
  }

  &__main {
    padding-top: 10vh;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    text-align: center;
    margin-bottom: 40px;
  }

  &__link {
    padding-bottom: 8px;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    text-decoration: none;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: $colorWhite;
    border-bottom: 1px solid transparent;
    transition: border-bottom 0.3s;

    &:hover {
      border-bottom: 1px solid $colorWhite;
    }
  }
}
