@import "../utils/mixins";
@import "../utils/vars";
@import "../utils/extends";

.contacts {
  background-color: $colorWhite;

  &__wrapper {
    background-color: $backgroundColor;
    border-radius: 0 200px 0 0;
  }

  &__content {
    padding: 72px 0 75px;

    @include onTablet {
      padding: 180px 0 134px;
    }

    @include onDesctop {
      padding: 180px 0 110px;
      display: flex;
      justify-content: space-between;
    }
    @include onDesctopLarge {
      column-gap: 200px;
    }

  }

  &__message {
    margin-bottom: 80px;

    @include onTablet {
      margin-bottom: 120px;
    }

    @include onDesctop {
      flex: 1 1 50%;
    }

    @include onDesctopLarge {
      flex: 1 1 42%;
    }
  }

  &__title {
    margin: 0 0 48px;
    width: 60%;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 32px;
    line-height: 130%;
    color: $colorWhite;

    &--contact {
      line-height: 150%;
    }

    @include onTablet {
      margin: 0 0 56px;
      line-height: 150%;
    }

    @include onDesctop {
      width: 100%;
    }
  }

  &__form-field {
    margin: 0 0 40px;
    padding: 2px 1px 16px;
    display: block;
    width: 100%;
    outline: none;
    border: 0;
    border-bottom: 1px solid $colorWhite;
    cursor: pointer;

    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    background-color: $backgroundColor;
    color: $colorWhite;

    @include onTablet {
      width: 65%;
    }

    @include onDesctop {
      width: 83%;
    }

    @include onDesctopLarge {
      width: 100%;
    }

    &:focus {
      border-bottom: 2px solid $colorWhite;
    }

    &::placeholder {
      font-family: "Poppins", sans-serif;
      font-weight: 600;
      font-size: 15px;
      line-height: 22px;
      color: $colorWhite;
      opacity: 0.7;
    }

    &--textarea {
      height: 35px;
      padding: 2px 1px 0;
      margin: 0 0 56px;
      resize: none;

      @include onTablet {
        margin: 0 0 80px;
      }
    }
  }

  &__button {
    width: 100%;
    border: 1px solid transparent;
    @extend %button;
    cursor: pointer;

    @include onTablet {
      width: 264px;
    }
  }

  &__contact {
    @include onDesctop {
      flex: 1 1 50%;
    }

    @include onDesctopLarge {
      flex: 1 1 42%;
    }
  }

  &__description {
    margin: 0 0 8px;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: $colorWhite;
    opacity: 0.5;

    &--last {
      margin: 0 0 18px;
    }
  }

  &__data {
    margin: 0 0 40px;
    display: inline-block;
    text-decoration: none;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: $colorWhite;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.05);
    }

    @include onTablet {
      margin: 0 0 48px;
    }
  }

  &__icons {
    padding-top: 3px;
    display: flex;
    align-items: baseline;
    column-gap: 40px;
    height: 24px;
  }

  &__icon-link {
    width: 24px;
    height: 24px;
  }

  &__icon {
    display: block;
    @extend %objectCenter;
    transition: background-image 0.3s;

    &--facebook {
      background-image: url("../images/icons/facebook.svg");

      &:hover {
        background-image: url("../images/icons/facebook-hover.svg");
      }
    }

    &--twitter {
      background-image: url("../images/icons/twitter.svg");

      &:hover {
        background-image: url("../images/icons/twitter-hover.svg");
      }
    }

    &--instagram {
      background-image: url("../images/icons/instagram.svg");

      &:hover {
        background-image: url("../images/icons/instagram-hover.svg");
      }
    }
  }
}
