@import "../utils/mixins";
@import "../utils/extends";
@import "../utils/vars";

.footer {
  padding: 11px 0 80px;
  background-color: $backgroundColor;

  &__container {
    @include onDesctop {
      display: flex;
      justify-content: space-between;
    }
  }

  &__logo {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 25px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    @include onTablet {
      display: flex;
      column-gap: 40px;
      justify-content: space-between;
      align-items: center;
    }

    @include onDesctop {
      column-gap: 64px;
    }
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onTablet {
      display: inline-block;
      margin-bottom: 0;
    }
  }

  &__link {
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-size: 13px;
    line-height: 150%;
    letter-spacing: 3px;
    text-transform: uppercase;
    text-decoration: none;
    @extend %linksHover;
  }
}
