@import "../utils/mixins";
@import "../utils/vars";
@import "../utils/extends";

.vision {
  background-color: $colorWhite;
  width: 100%;
  overflow: hidden;
  position: relative;

  &__decoration-wrapper {
    display: none;

    @include onTablet {
      box-sizing: border-box;
      display: block;
      margin: 0 auto;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      height: 740px;
      width: 1024px;
    }

    @include onDesctop {
      height: 690px;
      width: 1600px;
    }
  }

  &__decoretion {
    position: relative;
  }

  &__content {
    position: relative;
    z-index: 5;
    padding: 72px 0;
    max-width: 440px;
    margin: 0 auto;

    @include onTablet {
      padding: 200px 0;
      max-width: 560px;
    }

    @include onDesctop {
      padding: 198px;
      max-width: 754px;
    }
  }

  &__title {
    margin: 0 0 24px;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 44px;
    line-height: 120%;
    text-align: center;
    color: $colorTitle;

    @include onTablet {
      font-size: 52px;
    }

    @include onDesctop {
      line-height: 150%;
    }
  }

  &__text {
    margin: 0 0 56px;
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    text-align: center;

    @include onTablet {
      margin: 0 0 64px;
      font-size: 24px;
    }
  }

  &__link {
    @extend %button;
    cursor: pointer;

    @include onTablet {
      width: 264px;
      margin: 0 auto;
    }
  }

  &__icon {
    display: none;

    @include onTablet {
      position: absolute;
      display: inline-block;
      background-size: cover;
      background-repeat: no-repeat;

      &--yellow1 {
        top: 99px;
        left: 299px;
        background-image: url(../images/vision/yellow-st.svg);
        height: 46px;
        width: 32px;
      }

      &--yellow2 {
        top: 450px;
        left: 315px;
        background-image: url(../images/vision/yellow-sb.svg);
        height: 46px;
        width: 32px;
      }

      &--green1 {
        top: 288px;
        left: 138px;
        background-image: url(../images/vision/green.svg);
        height: 144px;
        width: 100px;
      }

      &--green2 {
        top: 63px;
        right: 133px;
        background-image: url(../images/vision/green.svg);
        height: 144px;
        width: 100px;
      }

      &--orange1 {
        top: 411px;
        right: 220px;
        background-image: url(../images/vision/orange.svg);
        height: 86px;
        width: 60px;
      }

      &--purp2 {
        top: 287px;
        right: 349px;
        background-image: url(../images/vision/pur-sb.svg);
        height: 54px;
        width: 37px;
      }
    }

    @include onDesctop {
      &--blueS {
        top: 370px;
        left: 82px;
        background-image: url(../images/vision/blue-small.svg);
        height: 68px;
        width: 47px;
      }

      &--orange1 {
        top: 72px;
